:root {
  --main-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --main-light: #fcfcfc;
  --main-dark: #131313;
  --accent-color: #f8d24e;
  --main-background: #84a178;
  --card-green: #aec6a5;
  --card-yellow: #efd478;
  --card-orange: #d6936d;
  --default-btn: #f8d24e;
  --hover-btn: #0a8817;
  --light-btn-text: #fcfcfc;
  --dark-btn-text: #000;
  --main-animation: 250ms cubic-bezier(.4, 0, .2, 1) 0s;
}

@keyframes random-shake {
  0% {
    transform: translate(1px, 1px)rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px)rotate(-1deg);
  }

  20% {
    transform: translate(-3px)rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px)rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px)rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px)rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px)rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px)rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px)rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px)rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px)rotate(-1deg);
  }
}

@keyframes small-shake {
  0% {
    transform: translate(0)rotate(0deg);
  }

  20% {
    transform: translate(0)rotate(1deg);
  }

  40% {
    transform: translate(0)rotate(-1deg);
  }

  60% {
    transform: translate(0)rotate(1deg);
  }

  80% {
    transform: translate(0)rotate(-1deg);
  }

  100% {
    transform: translate(0)rotate(0deg);
  }
}

